import { NgModule, Component } from '@angular/core';
import { Router, Routes, RouterModule } from '@angular/router';

import { HomeComponent } from "./home/home.component";
import { AboutUsComponent } from "./about-us/about-us.component";
import { ContactUSComponent } from "./contact-us/contact-us.component";
import { BlogComponent } from "./blog/blog.component";
import { GenComponent } from './gen/gen.component';
import { CleanServicesComponent } from "./clean-services/clean-services.component";

import { TriangularHomeComponent } from "./triangular-home/triangular-home.component";
import { TriangularBlogComponent } from "./triangular-blog/triangular-blog.component";
import {TriangularContactComponent} from './triangular-contact/triangular-contact.component'
import { TriangularAboutUsComponent } from "./triangular-about-us/triangular-about-us.component";
const routes: Routes = [
  
    { path: '', redirectTo: 'Meglorate', pathMatch: 'full' },
    {path: 'Meglorate' ,component: GenComponent},
    { path: 'triangular', component: TriangularHomeComponent},
    { path: 'prudent', component: CleanServicesComponent },
    {path: 'home', component: HomeComponent},
    {path: 'about-us', component: AboutUsComponent},
    {path:'triangular-about-us', component:TriangularAboutUsComponent},
    {path:'triangular-blog', component:TriangularBlogComponent},
    {path:"triangular-contact", component: TriangularContactComponent},
    {path:"blog", component: BlogComponent},
  
    { path: 'meg', redirectTo: 'home', pathMatch: 'full'}
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
