import { Component, OnInit, HostListener, Inject } from '@angular/core';
import { NgForm } from "@angular/forms";
import { DOCUMENT } from "@angular/common";
import { ServiceService } from "../service.service";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  constructor(@Inject(DOCUMENT) private document: Document, private s: ServiceService) { }

  @HostListener('window:scroll', ['$event']) // for window scroll events

  onWindowScroll() {

    if (document.body.scrollTop > 625 ||
      document.documentElement.scrollTop > 625) {
      document.getElementById('oii').classList.add('yuod');
      
      document.getElementById('what').classList.add('show');
    }
    if (document.body.scrollTop > 760 ||
      document.documentElement.scrollTop > 760) {
      console.info('uiuiu')
      document.getElementById('oiui').classList.add('yuod');
      document.getElementById('lki').classList.add('show');
    }
    
    if (document.body.scrollTop > 305 ||
      document.documentElement.scrollTop > 305) {
      // console.info("omo")
      document.getElementById('lio').classList.add('sd');


    }

  }
  sent() {
    this.document.getElementById('toast').classList.add('show');
  }

  ngOnInit() { }

  submitForm(form: NgForm) {
    this.s.sendFeedback(form.value);
    this.sent();
    form.reset();
  }
}