import { DOCUMENT } from '@angular/common';
import { Component, HostListener, Inject, OnInit } from '@angular/core';
@Component({
  selector: 'app-triangular-nav',
  templateUrl: './triangular-nav.component.html',
  styleUrls: ['./triangular-nav.component.css']
})
export class TriangularNavComponent implements OnInit {

  
  constructor(@Inject(DOCUMENT) private document: Document,) { }
  @HostListener('window:scroll', ['$event'])
  onWindowScroll(){
    if(document.body.scrollTop > 5 ||
      document.documentElement.scrollTop > 5){
        document.getElementsByClassName('carryLink').item(0).classList.add('blue')
        document.getElementsByClassName('carryLink').item(1).classList.add('blue')
        document.getElementsByClassName('carryLink').item(2).classList.add('blue')
        document.getElementsByClassName('carryLink').item(3).classList.add('blue')
        document.getElementById('me').classList.add('navStyle') }
        else {
        document.getElementById('me').classList.remove('navStyle');    
        document.getElementsByClassName('carryLink').item(0).classList.remove('blue')
        document.getElementsByClassName('carryLink').item(1).classList.remove('blue')
        document.getElementsByClassName('carryLink').item(2).classList.remove('blue')
        document.getElementsByClassName('carryLink').item(3).classList.remove('blue') }

  }

  ngOnInit() {
  }

}
