import { Component, OnInit, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-triangular-home',
  templateUrl: './triangular-home.component.html',
  styleUrls: ['./triangular-home.component.css']
})
export class TriangularHomeComponent implements OnInit {

  constructor(@Inject(DOCUMENT) private document: Document) { }
  location: Location;
  ngOnInit() {
      this.location = {
          latitude: -28.68352,
          longitude: -147.20785,
          mapType : 'satellite'
      }
    this.looper()
  }
  
  looper() {
    setTimeout(() => {
      this.fxnsl();
      this.looper();
    }, 7000);
  }
  i = 0;
  fxnsl() {
    var arr = [
      this.document.getElementById('img3'),
      this.document.getElementById('img2'),
      this.document.getElementById('img1'),
      this.document.getElementById('img4'),
       this.document.getElementById('img5'),
      this.document.getElementById('img6')];

    for (const x of arr) {
      x.style.display = "none";
    }
    if (this.i > arr.length - 1) {
      this.i = 0;
      const show = arr[this.i].style.display = "block";
    }
    const show = arr[this.i].style.display = "block";
    this.i++;
  }
}

interface Location {
  latitude: number;
  longitude: number
  mapType : string;
}