import { DOCUMENT } from '@angular/common';
import { Component, HostListener, Inject, OnInit } from '@angular/core';

@Component({
  selector: 'app-clean-nav',
  templateUrl: './clean-nav.component.html',
  styleUrls: ['./clean-nav.component.css']
})
export class CleanNavComponent implements OnInit {

  constructor(@Inject(DOCUMENT) private document: Document,) { }
  @HostListener('window:scroll', ['$event'])
  onWindowScroll(){
    if(document.body.scrollTop > 5 ||
      document.documentElement.scrollTop > 5){
        document.getElementById('ppo').style.borderBottom='0px solid #ccc';
        document.getElementById('navv').classList.add('navStyle') }
        else {document.getElementById('navv').classList.remove('navStyle');    
        document.getElementById('ppo').style.borderBottom = '2px solid #ccc'; }

  }

  ngOnInit() {
  }

}
